html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  background: hsl(0deg, 0%, 100%);
  font-family: "Kumbh Sans", sans-serif !important;
}

ul, li {
  list-style: none;
  margin: 0;
  padding: 0;
}

a,
a:hover {
  text-decoration: none;
  outline: none;
}

h1, h3 {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3 {
  margin: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
  outline: none;
}

body {
  font-size: 1rem;
}

#wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

header .header-wrapper {
  height: 5rem;
  display: flex;
  align-items: center;
  border-bottom: 0.0625rem solid hsla(220deg, 14%, 75%, 0.4);
}

main {
  height: 100%;
  flex-grow: 1;
}

footer .footer-wrapper {
  padding: 1.25rem 0.625rem;
  border-top: 0.0625rem solid hsl(220deg, 14%, 75%);
  font-size: 0.75rem;
}

.header-wrapper,
.content-wrapper,
.footer-wrapper {
  width: 100%;
  max-width: 90rem;
  margin: 0 auto;
  position: relative;
}

.grid-container {
  display: grid;
  grid-gap: 0.625rem;
  grid-template-columns: auto;
  grid-template-rows: repeat(auto, 1fr);
  grid-template-areas: "slides" "content";
}
@media (min-width: 71.875em) {
  .grid-container {
    grid-template-columns: 35% 65%;
    grid-template-areas: "slides content content content";
  }
}
.bg-img {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.product-details {
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .product-details {
    width: 90%;
    padding: 4.375rem 0.625rem;
  }
}

.product-content {
